import Menu from "./Components/Menu";
import ItemList from "./Components/ItemList";
import AddNewItem from "./Components/AddNewItem";

import { useEffect, useState } from "react";
import Popup from "./Components/Popup";

const defaultList = [
  {
    title: "Example",
    tasks: [
      {
        name: "Task 1",
        completed: false,
      },
      {
        name: "Task 2",
        completed: false,
      },
      {
        name: "Task 3",
        completed: true,
      },
    ],
  },
  {
    title: "Example 2",
    tasks: [
      {
        name: "Task 1",
        completed: false,
      },
      {
        name: "Task 2",
        completed: false,
      },
    ],
  },
];

const loadTasks = () => {
  const taskListJson = localStorage.getItem("taskList");
  if (taskListJson === null) return defaultList;

  return JSON.parse(taskListJson);
};

const loadCurrent = () => {
  const currentList = localStorage.getItem("currentList");
  if (currentList === null) return 0;

  return Number(currentList);
};

const loadHidden = () => {
  const hidden = localStorage.getItem("hiddenCompleted");
  if (hidden === null) return true;

  return hidden === "true";
};

const saveChanges = (taskList, taskID, itemID) => {
  const prev = taskList[taskID].tasks[itemID].completed;

  if (!prev) {
    // Play sound
    new Audio("./sound.wav").play();
  }
  taskList[taskID].tasks[itemID].completed = !prev;
  localStorage.setItem("taskList", JSON.stringify(taskList));
  return JSON.parse(JSON.stringify(taskList));
};

const saveNewItem = (taskList, taskID, itemName) => {
  taskList[taskID].tasks.push({
    name: itemName,
    completed: false,
  });
  localStorage.setItem("taskList", JSON.stringify(taskList));
  return JSON.parse(JSON.stringify(taskList));
};

const saveNewTask = (taskList, taskName) => {
  taskList.push({
    title: taskName,
    tasks: [],
  });
  localStorage.setItem("taskList", JSON.stringify(taskList));
  return JSON.parse(JSON.stringify(taskList));
};

const saveDeleteTask = (taskList, taskID) => {
  taskList.pop(taskID);
  localStorage.setItem("taskList", JSON.stringify(taskList));
  return JSON.parse(JSON.stringify(taskList));
};

const saveCurrent = (id) => {
  localStorage.setItem("currentList", id);
};

const saveHidden = (hidden) => {
  localStorage.setItem("hiddenCompleted", JSON.stringify(hidden));
};

function App() {
  const [taskList, setTaskList] = useState();
  const [currentList, setCurrentList] = useState();
  const [hidden, setHidden] = useState();
  const [popup, setPopup] = useState(false);

  useEffect(() => {
    const taskListToLoad = loadTasks();
    setTaskList(taskListToLoad);

    const currentID = loadCurrent();
    setCurrentList(currentID);

    const hiddenToLoad = loadHidden();
    setHidden(hiddenToLoad);
  }, []);

  const changeStatus = (taskID, itemID) => {
    const newList = saveChanges(taskList, taskID, itemID);
    setTaskList(newList);
  };

  const changeCurrent = (id) => {
    if (id < taskList.length) {
      setCurrentList(id);
      saveCurrent(id);
    }
  };

  const changeHidden = () => {
    const newHidden = !hidden;
    saveHidden(newHidden);
    setHidden(newHidden);
  };

  const saveItem = (taskID, itemName) => {
    if (itemName !== "") {
      const newList = saveNewItem(taskList, taskID, itemName);
      setTaskList(newList);
    }
  };

  const saveTask = (taskName) => {
    if (taskName !== "") {
      const newList = saveNewTask(taskList, taskName);
      setTaskList(newList);
      changeCurrent(newList.length - 1);
    }
  };

  const deleteTask = (taskId) => {
    const newList = saveDeleteTask(taskList, taskId);
    if (taskId === currentList) {
      changeCurrent(newList.length - 1);
    }
    setTaskList(newList);
  };

  return (
    <div className="flex flex-col" style={{ width: "100vw" }}>
      <Menu
        tasks={taskList}
        current={currentList}
        changeCurrent={changeCurrent}
        changeHidden={changeHidden}
        hidden={hidden}
        openPopup={() => setPopup(true)}
        deleteTask={deleteTask}
      />
      <ItemList
        tasks={taskList}
        current={currentList}
        changeStatus={changeStatus}
        hidden={hidden}
      />
      <AddNewItem saveItem={saveItem} current={currentList} />
      <Popup
        show={popup}
        closePopup={() => setPopup(false)}
        saveTask={saveTask}
      />
    </div>
  );
}

export default App;
