import React, { useState } from "react";
import { FiMenu, FiTrash2 } from "react-icons/fi";
import { GoEye, GoEyeClosed } from "react-icons/go";

import { FaPlus } from "react-icons/fa";

export default function Menu(props) {
  const [show, setShow] = useState(false);
  if (props.tasks === undefined || props.current === undefined)
    return <div>No tabs</div>;

  var currentList = props.tasks[props.current];

  if (currentList === undefined) currentList = { title: "No tabs yet" };

  return (
    <div>
      <div className="fixed top-0 left-0 w-full bg-white flex items-center justify-between p-4">
        <FiMenu onClick={() => setShow(true)} className="text-2xl" />
        <h1 className="font-black text-blue-900 text-xl">
          {currentList.title}
        </h1>
        <div onClick={props.changeHidden} className="text-2xl">
          {!props.hidden ? <GoEyeClosed /> : <GoEye />}
        </div>
      </div>
      <div className={"h-full w-full" + (show ? "" : " hidden")}>
        <div
          className="fixed top-0 left-0 z-20 w-full h-full bg-black opacity-50"
          onClick={() => setShow(false)}
        ></div>
        <div className="fixed top-0 left-0 z-20 w-4/5 h-full bg-blue-50 flex flex-col ">
          <div
            className="w-full p-5 items-center justify-between flex text-blue-900 border-b-2 border-blue-900"
            onClick={props.openPopup}
          >
            <h1 className="font-black text-xl">My ToDo</h1>
            <FaPlus className="text-2xl" />
          </div>

          <div className="w-full h-full flex flex-col overflow-y-auto">
            {props.tasks &&
              props.tasks.map((item, id) => {
                return (
                  <MenuItem
                    shown={id === props.current}
                    item={item}
                    key={id}
                    click={() => {
                      props.changeCurrent(id);
                      setShow(false);
                    }}
                    deleteTask={() => props.deleteTask(id)}
                  />
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
}

function MenuItem(props) {
  return (
    <div
      className={
        "p-5 text-lg flex items-center justify-between" +
        (props.shown ? " bg-blue-200" : "")
      }
      onClick={props.click}
    >
      <p>{props.item.title}</p>
      <FiTrash2
        className="text-2xl text-gray-700"
        onClick={(e) => {
          e.stopPropagation();
          props.deleteTask();
        }}
      />
    </div>
  );
}
