import React from "react";
import Item from "./Item";

export default function ItemList(props) {
  if (props.tasks === undefined || props.current === undefined)
    return <div>No tasks for this tab</div>;

  const currentList = props.tasks[props.current];

  if (currentList === undefined) {
    return (
      <div className="w-full text-center">
        Begin by creating a tab inside the menu
      </div>
    );
  }

  const changeStatus = (itemID) => {
    props.changeStatus(props.current, itemID);
  };

  return (
    <div className="flex flex-col w-full p-5 pt-16">
      {currentList.tasks.map((item, id) => {
        if (!props.hidden || !item.completed)
          return (
            <Item task={item} key={id} id={id} changeStatus={changeStatus} />
          );
        return <></>;
      })}
    </div>
  );
}
