import React, { useState } from "react";

export default function Popup(props) {
  const [task, setTask] = useState("");

  const addTask = (e) => {
    e.preventDefault();
    props.saveTask(task);
    setTask("");
    props.closePopup();
  };

  return (
    <div
      className={
        "absolute left-0 top-0 w-full h-full" + (props.show ? "" : " hidden")
      }
    >
      <div
        className="absolute top-0 left-0 z-30 w-full h-full bg-black opacity-50"
        onClick={props.closePopup}
      ></div>
      <div className="absolute top-0 left-0 z-30 w-full h-full flex items-center justify-center">
        <form
          className="bg-white w-2/3 rounded-sm flex flex-col p-3"
          onSubmit={addTask}
        >
          <input
            type="text"
            onChange={(e) => setTask(e.target.value)}
            className="px-4 py-3 rounded-l-sm w-full h-12 focus:outline-none border border-gray-200 focus:border-blue-900 mb-3"
            autoFocus
            value={task}
          />
          <input type="submit" value="Save" className="p-3" />
        </form>
      </div>
    </div>
  );
}
