import React, { useState } from "react";
import { FaPlus } from "react-icons/fa";

export default function AddNewItem(props) {
  const [itemName, setItemName] = useState("");

  const saveItem = (e) => {
    e.preventDefault();
    props.saveItem(props.current, itemName);
    setItemName("");
  };

  return (
    <form
      onSubmit={saveItem}
      className="w-full p-3 bg-blue-50 fixed z-10 bottom-0 left-0 flex items-center justify-center"
    >
      <div className="border border-gray-200 rounded-sm w-full flex items-center justify-center focus-within:border-blue-200">
        <input
          type="text"
          className="px-4 py-3 rounded-l-sm w-full h-12 focus:outline-none"
          onChange={(e) => setItemName(e.target.value)}
          value={itemName}
        />
        <div className="rounded-r-sm text-xl bg-blue-200 h-12 w-16 flex items-center justify-center text-blue-900">
          <FaPlus />
        </div>
      </div>
    </form>
  );
}
